import counter from "./counter.vue"
export default {
	components: {
		counter
	},
	props: {
		showinfo: Object,
		goodsinfo: Object,
		goodsdetail: Object
	},
	data() {
		return {

		}
	},
	methods: {
		sub(){
			this.$emit("sub")
		},
		add(){
			this.$emit("add")
		},
		hidePop() {
			// this.showinfo.showspec = false
			this.$emit("hideSpec")
		},
		handleSelecte(item) {
			//选择规格
			// this.goodsinfo.spec = item;不合理 不能直接更改父级数据
			// 正确做法 子组件注册事件，父组件执行
			this.$emit("setSpec", item)

		}
	}
}
